.navContainer {
    display: flex;
    width: 100%;
    height: 65px;
    background: #FFFFFF;
    border: 1px solid #E6E6E6;
}

.contentContainer {
    width: 70%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navLink {
    font-family: 'OpenSans';
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 1.09091px;
    color: #48555B;
    text-decoration: none;
    margin-left: 30px;
    transition: 0.1s ease-in-out;
}

.navLink:hover{
    margin-left: 26px;
    font-size: 13px;
    line-height: 15px;
    transition: 0.3s ease-in-out;
    border-bottom: 1px solid gray;
}